import moment from 'moment'


const publicFilters = {
    // 日期格式化
    timeFormat:function(dataStr, pattern = 'YYYY-MM-DD HH:mm:ss') {
        if (dataStr) {
          return moment(dataStr).format(pattern)
        }
      }
      
}


export default publicFilters 


