import Vue from 'vue'
import Router from 'vue-router'

//路由懒加载
const home = () => import('@/pages/home/home')
const course = () => import('@/pages/course/course')
const subject = () => import('@/pages/subject/subject')
const activity = () => import('@/pages/activity/activity')
const share = () => import('@/pages/share/share')
const knowledge = () => import('@/pages/knowledge/knowledge')

Vue.use(Router)

const router = new Router({
  routes: [
    {
      path: '/',
      name: 'Home',
      component: home,
      meta: {
        title: '首页'
      }
    },
    {
      path: '/course',
      name: 'course',
      component: course,
      meta: {
        title: '课程'
      }
    },
    {
      path: '/subject',
      name: 'subject',
      component: subject,
      meta: {
        title: '专题'
      }
    },
    {
      path: '/activity',
      name: 'activity',
      component: activity,
      meta: {
        title: '活动'
      }
    },
    {
      path: '/share',
      name: 'share',
      component: share,
      meta: {
        title: '纷享'
      }
    },
    {
      path: '/knowledge',
      name: 'knowledge',
      component: knowledge,
      meta: {
        title: '纷享'
      }
    }
  ]
})

router.beforeEach((to, from, next) => {
  /* 路由发生变化修改页面title */
  if (to.meta.title) {
    document.title = '知学云 - ' + to.meta.title
  }
  next()
})


export default router



