// The Vue build version to load with the `import` command
// (runtime-only or standalone) has been set in webpack.base.conf with an alias.
import Vue from 'vue'
import App from './App'
import router from './router'
import './style/common.css'                             //导入公共样式  
import './style/iconfont/iconfont.css'

import global_navBar from './plugins/global_navBar.js'  // 全局插件引入
Vue.use(global_navBar)                                  // 全局插件使用

import VueAwesomeSwiper from 'vue-awesome-swiper'       // swiper滑动组件
import 'swiper/dist/css/swiper.css'
Vue.use(VueAwesomeSwiper)

import VueCookie from 'vue-cookie' // api: https://github.com/alfhen/vue-cookie
Vue.use(VueCookie)

import ElementUI from 'element-ui';                      //引入element ui
import 'element-ui/lib/theme-chalk/index.css';
Vue.use(ElementUI);

import publicFilters from '@/utils/publicFilters.js'  //moment.js 日期格式化
Object.keys(publicFilters).forEach(key => {
  Vue.filter(key, publicFilters[key])
})

import 'viewerjs/dist/viewer.css'        //点击预览图片功能
import Viewer from 'v-viewer'
// 这里是插件的默认设置
Viewer.setDefaults({
    zIndex: 9999,
    "inline": false,
    "button": true,
    "navbar": true,
    "title": true,
    "toolbar": true,
    "tooltip": true,
    "movable": true,
    "zoomable": true,
    "rotatable": true,
    "scalable": true,
    "transition": true,
    "fullscreen": true,
    "keyboard": true,
})
Vue.use(Viewer)

// 挂载全局
// import httpRequest from '@/utils/httpRequest'      // api: https://github.com/axios/axios
// Vue.prototype.$http = httpRequest // ajax请求方法

Vue.config.productionTip = false

/* eslint-disable no-new */
new Vue({
  el: '#app',
  router,
  components: { App },
  template: '<App/>'
})



