<template>
    <div class="navBar-box">
      <div class="navBar-header">
        <div class="navBar-top">
            <ul>
              <li>
                <span class="iconfont icon-phone"></span>
                <span>下载app</span>
              </li>
              <li>
                <span></span>
                <span>我的消息</span>
              </li>
              <li>
                <span class="iconfont icon-computer"></span>
                <span>管理后台</span>
              </li>
              <li>
                <span></span>
                <span>思睦09</span>
              </li>
            </ul>
        </div>
        <div class="navBar">
          <div class="navBar-left">
            <img src="https://zxy9.zhixueyun.com/default/M00/02/77/Ci7mTVt_oVOAfR-OAAATKv9qYls340.png" alt />
          </div>
          <div class="navBar-center">
            <ul>
              <li v-for="(item,index) of navBarList" :key="index">
                <router-link :to="item.url" :class="navBarActive == item.id ? 'router-link-exact-active' : '' ">{{item.title}}</router-link>
              </li>
            </ul>
          </div>
          <div class="navBar-right">
              <div class="inputBox">
                <input type="text" placeholder="请输入您要搜索的内容" />
              </div>
              <div class="buttonBox">
                <span class="iconfont icon-search"></span>
                <span>搜索</span>
              </div>
          </div>
        </div>
      </div>
    </div>
</template>

<script>
export default {
  name: "navBar",
  data() {
    return {
      navBarList: [
        {
          id:0,
          title: "首页",
          url: "/"
        },
        {
          id:1,
          title: "课程",
          url: "/course"
        },
        {
          id:3,
          title: "专题",
          url: "/subject"
        },
        {
          id:2,
          title: "活动",
          url: "/activity"
        },
        {
          id:5,
          title: "纷享",
          url: "/share"
        },
        {id:4,
          title: "知识",
          url: "/knowledge"
        },
        {
          id:5,
          title: "知学来画",
          url: "/"
        },
        {id:4,
          title: "应用",
          url: "/"
        }
      ]
    };
  },
  props:{
    navBarActive:{
      type:Number
    }
  }
};
</script>


<style lang="scss" scoped>
  .navBar-box{
    width: 100%;
    height: 84px;
  }
.navBar-header {
  position: fixed;
  top: 0;
  left: 0;
  z-index: 100;
  width: 100%;
  height: 84px;
  background:#fff;
  box-shadow: 0 1px 1px rgba(0, 0, 0, 0.1);
}
.navBar-top{
  width: 1200px;
  margin: 0 auto;
  height: 30px;
  line-height: 30px;
  ul{
    display: flex;
    justify-content: flex-end;
    li{
      padding: 0 30px;
      position: relative;
      .iconfont{
        font-size:18px;
      }
      &::before{
        content: '';
        position: absolute;
        width:1px;
        height: 16px;
        background:#ddd;
        top:50%;
        right:0;
        transform: translateY(-50%);
      }
      &:last-of-type{
        padding-right: 0;
        &::before{
          content: '';
          width:0;
        }
      }
    }

  }
}
.navBar-left {
  display: flex;
  img {
    width: 120px;
    height: 30px;
  }
}
.navBar {
  display: flex;
  margin: auto;
  justify-content: space-between;
  align-items: center;
  width: 1200px;
  background: #fff;
}
.navBar .navBar-center ul {
  display: flex;
  height: 54px;
  line-height: 54px;
}
.navBar .navBar-center ul li a {
  width: 100%;
  height: 100%;
  display: inline-block;
  padding: 10px 20px;
  text-align: center;
  font-size: 16px;
}
.navBar ul {
  display: flex;
  align-items: center;
}
.navBar-right{
  display: flex;
  align-items:center;
  width: 325px;
  height: 35px;
  line-height: 35px;
  border:1px solid #0084CF;
  border-radius: 30px;
  overflow: hidden;
  .inputBox{
    width: 252px;
    input{
      width:100%;
      height: 35px;
      padding:0 10px;
    }
    input::placeholder {
        color: #c6c6c6;
    }
  }
  .buttonBox{
    text-align:center;
    width: 73px;
    background:#0084CF;
    color:#fff;
  }
}
/* 点击后的效果 router-link 即a 标签自带高亮效果*/
.navBar ul li .router-link-exact-active {
  color: #3091f2;
}
</style>



